	methods: {
	getTaskList() {
		var _this = this
		var params = {				
			pageIndex: _this.pageIndex,
			pageSize: _this.pageSize			
		}
		uni.request({
			url: _this.ApiGateWay + '/sale/Member/GetUserAchievementDesc',
			data: params,
			method: 'POST',
			dataType: 'json',
			header: {
