			}
	
	
		})
	
	},
	getTask(taskId) {
		var _this = this;
	
		uni.showLoading({
			title: "加载中..."
		})
		uni.request({
			url: _this.ApiGateWay + '/sale/Member/MemberGetTask?taskId=' + taskId,
			header: {
				Authorization: _this.token
			},
			method: 'POST',
			dataType: 'json',
			success: function(res) {
	
				if (Helper.CheckData(res)) {
					res = res.data;					
					uni.showModal({
						title: '提示',
						content: '领取成功',
						confirmColor: '#5A83E9',
						showCancel: false,
					});
				} else {
					uni.showToast({
						title: res.data.errorMessage,
						icon: "none"
					})
				}
	
			},
			complete() {
				uni.hideLoading();
			},
			fail: function(res) {
				uni.stopPullDownRefresh();
			}
		});
	
	
	
